import {
	uniNavBar
} from '@dcloudio/uni-ui'
export default {
	components: {
		uniNavBar
	},
	onShow: function() {
		try {
			const value = uni.getStorageSync('editData');
			if (value) {
				this.listdata = value;
				try {
					uni.removeStorageSync('editData');
				} catch (e) {
					// error
				}
			}
		} catch (e) {
			// error
		}
	},
	onLoad: function(option) {
		if (option.id) {
			this.dataId = option.id;
		}
	},
	props: {},
	onShow() {
		if (this.dataId) {
			this.getoneData(this.dataId);
		}
	},
	data() {
		return {
			dataId: '',
			listdata: {},
			Token:uni.getStorageSync('userId') || '',
		}
	},
	methods: {
		toback() {
			uni.navigateBack({
				delta: 1,
				animationType: 'pop-out',
				animationDuration: 200
			});
		},
		// 获取数据
		getoneData(id) {
			uniCloud.callFunction({
				name: 'get',
				data: {
					token:this.Token,
					id: id,
					pageNum: 0
				}
			}).then((res) => {
				uni.hideLoading();
				if (res.result.total > 0) {
					this.listdata = res.result.list[0]
				}
			}).catch((err) => {
				uni.hideLoading();
				uni.showToast({
					icon: 'none',
					mask: true,
					title: '未查询到相关信息'
				})
			})
		},
		// 删除数据
		deldata() {
			const _this = this;
			uni.showModal({
				title: '提示',
				content: '是否确认删除此条数据？',
				success: function(res) {
					if (res.confirm) {
						uniCloud.callFunction({
							name: 'remove',
							data: {
								dataId: _this.listdata._id,
								token:_this.Token
							}
						}).then((res) => {
							uni.showToast({
								title: `${res.result.msg}`,
								duration: 2000
							});
							setTimeout(() => {
								_this.toback()
							}, 2000)

						}).catch((err) => {
							uni.showModal({
								content: `操作失败，请重新操作`,
								showCancel: false
							})
							console.error(err)
						})
					} else if (res.cancel) {
						console.log('用户点击取消');
					}
				}
			});
		},
		// 修改数据
		editdata() {
			this.listdata.picture = this.listdata.picture ? this.listdata.picture : '';
			console.log("this.listdata:",this.listdata)
			delete this.listdata.uniCloudClientInfo;
			delete this.listdata.useType.uniCloudClientInfo;
			let _item = JSON.stringify(this.listdata)
			uni.redirectTo({
				url: './edit?item=' + _item
			});
		}
	}
}
